footer {
    display: flex;
    flex-direction: row;
    justify-content: center;
    position: absolute;
    align-items: center;
    margin-top: 50px;
    width: 100%;
    height: 100px;
    background-color: var(--color-light);
    backdrop-filter: blur(1px);
    transition: ease-in .2s;
}

footer:hover {
    background-color: rgba(255, 255, 255, 0.2);
    backdrop-filter: blur(2px);
}

footer > ul > li > p {
    text-align: center;
    font-size: large;
}

@media screen and (max-width: 400px) {
    footer > ul > li > p {
        font-size: medium;
    }
}